body{
font-family: Georgia;
font-weight: normal;
font-size: 2em;
}

.App {
  
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 80px;
}

.App-header {
  background-color: #222;
  height: 150px;
  padding: 20px;
  color: white;
}

.App-intro {
  font-size: large;
}



.list-of-leads {
  border-top: 2px solid #000;
  border-bottom: 1px solid #000;
  padding-top: 5px;
  margin-top: 2rem;
  -webkit-transform: translateZ(0);
          transform: translateZ(0);
}
.menu-lead {
  border-bottom: 2px solid #000;
  border-top: 1px solid #000;
  padding-bottom: 2rem;
  padding-top: 2rem;
  margin-bottom: 5px;
  clear: both;
  overflow: hidden;
}

th,td {
  text-align: left;
}
.leaddetails-name{
  text-align: left;
  display: inline;
}
.lead-list {
  margin:1% 2% 5% 2%;
  border-width: 1px;
}
.l-list {
    margin:0% 2% 0% 3%;
    padding: 0 0 0% 0;

}
.add-lead, .add-account, .add-contact {
    margin:5% 1% 0% 4%;
}

.accdetails-name {
  text-align: left;
  display: inline;
}
.lead-details, .lead-form {
  margin:3% 2% 5% 2%;
  border-width: 1px;
}
.account-details, .account-form{
  margin:3% 2% 5% 2%;
  border-width: 1px;
}
.account-list {
  margin:1% 2% 5% 2%;
  border-width: 1px;
}
.a-list {
    margin:0% 2% 0% 3%;
    padding: 0 0 0% 0;
  }
.contact-details, .contact-form{
  margin:3% 2% 5% 2%;
  border-width: 1px;
}
.contact-list {
  margin:1% 2% 5% 2%;
  border-width: 1px;
}
.c-list {
    margin:0% 2% 0% 3%;
    padding: 0 0 0% 0;

}
.lead-full {
  margin:3% 2% 5% 2%;
  border-width: 1px;
  border-color: black;
}
.account-full {
  margin:3% 2% 5% 2%;
  border-width: 1px;
  border-color: black;
}
.contact-full{
  margin:3% 2% 5% 2%;
  border-width: 1px;
  border-color: black;
}
.lead-edit {
  margin:3% 2% 5% 2%;
  border-width: 1px;
  border-color: black;
}

.clearB {
  clear: both;
}
.pR{
  position: relative;
}
.tabDiv{
  border-bottom: 1px solid #f0f0f0;
  padding-top: 10px;
  padding-bottom: 5px;
  min-height: 17px;

}
.mDiv{
  padding-top: 10px;
  padding-bottom: 5px;
  min-height: 17px;
}

.fixed-width{
width:200px;
}
.lspace{
  padding: 5px 0 0 0;
}
.floatL{
  float: left;
}
.ltab_0{
width: 48%;
}
.ltab_1{
  width:48%;
  float: right!important;
}
input, select, textarea {
  border:0!important

}
input:focus, select:focus, textarea:focus {
  outline:none;
}
.tabDiv input:focus, select:focus, textarea:focus {
  border-bottom:1px solid #69cff3 !important;
}


.mandatory:after {
  content: " ";
  display: inline-block;
  width: 25px;
  height: 2px;
  background: #f27b77;
  position: absolute;
  bottom: -1px;
  left: 0;
}

.cvBtnHeader {
    height: 70px;
    width: 100%;
    position: relative;
    z-index: 13;
}
.cvpadding {
    padding: 0px 0 0px 0;
    background-color: #f8f8f8;
    border-bottom: 1px solid #eee;
}
.navbar {
  margin-bottom: 0px !important;
}


